import {observer} from "mobx-react-lite";
import NavBar from "../NavBar/NavBar";
import styles from './ForgotPass.module.scss'
import {useContext, useState} from "react";
import {Link} from "react-router-dom";
import {Context} from "../../index";
function ForgotPassword() {
    const {store} = useContext(Context)
    const [email, setEmail] = useState('nikitazhuk99@icloud.com')
    const [emailDirty, setEmailDirty] = useState(false)
    const [emailError, setEmailError] = useState(' ,    .')
    const emailHandler = (e) => {
        setEmail(e.target.value)
        const re = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
        if (!re.test(String(e.target.value).toLowerCase())) {
            setEmailError('  .')
        } else {
            setEmailError('')
        }
    }
    const BlurHandle = (e) => {
        switch (e.target.name) {
            case 'email':
                setEmailDirty(true)
                break
        }
    }
    return(
        <div>
            <NavBar/>
            <div className={styles.body}>
                <h2 className={styles.title}> </h2>
                <p style={{padding:'0px 10px'}}>   , '  .         .</p>
                <form autoComplete='true'>
                    <div className={styles.email}>
                        <input
                            onBlur={e => BlurHandle(e)}
                            onChange={(e) => emailHandler(e)}
                            type="text" name='email'
                            className={emailDirty && emailError ? styles.inputError : ''}
                            placeholder='example@spanish.com'
                        />
                        <label className={styles.emailLabel}></label>
                        {
                            emailDirty && emailError && <div className={styles.emailErrors}>{emailError}</div>

                        }
                    </div>
                </form>
                {
                    !emailError && email? <Link className={styles.button} to='/sendMessage' onClick={()=>store.sendRecoverMessage(email)}> </Link>
                        : <Link className={styles.button} to='#'> </Link>
                }
            </div>
        </div>
    )
}
export default observer(ForgotPassword)