import styles from './Mobile.module.scss'
import CancelIcon from '@mui/icons-material/Cancel';
import {Link, Navigate} from "react-router-dom";
import {useContext} from "react";
import {Context} from "../../index";
import {observer} from "mobx-react-lite";

function MobileMenu({setIsOpen}) {
    const {store} = useContext(Context);
    const logout = () =>{
        store.logout()
        setIsOpen(false)
    }
    return(
        <div className={styles.body}>
            <div className={styles.links}>
                { store.isAuth && <h2>, {store.user.name}!</h2>}
                <Link to='/' onClick={()=>setIsOpen(false)}> </Link>
                <Link to={store.isAuth?'/cabinet':'/auth/login'} onClick={()=>setIsOpen(false)}> </Link>
                <Link to='/AboutUs' onClick={()=>setIsOpen(false)}> </Link>
                <Link to='/feedbacks' onClick={()=>setIsOpen(false)}></Link>
                {
                    store.isAuth? <button onClick={logout}></button>
                        : <Link to='/auth/login'></Link>

                }
            </div>
            <CancelIcon style={{ marginTop: '30px' , color:'white' , height: '70px', width:'70px'}} onClick={()=>setIsOpen(false)}/>
        </div>
    )
}
export default observer(MobileMenu)